import React, { Component } from 'react';
import {BrowserRouter as Router, Route,Link,Switch,Redirect} from 'react-router-dom';
import Index from '../routers/Index/Index'
import GameIntroduced from '../routers/GameIntroduced/GameIntroduced';
import Company from '../routers/Company/Company';
import ContactUs from '../routers/ContactUs/ContactUs';
import Recruit from '../routers/Recruit/Recruit';
import ParentsCare from '../routers/ParentsCare/ParentsCare';
import Login from '../routers/Login/Login';
import Register from '../routers/Register/Register';
import System from '../routers/System/System';
import Protocol from '../routers/Protocol/Protocol';
import Scheme from '../routers/Scheme/Scheme';
import Dispute from  '../routers/Dispute/Dispute';
import Test from '../routers/Test/Test';
import Test2 from '../routers/Test2/Test2';
import './Public.css';
import jxtlogo from '../Image/Master/jxtlogo.png';
class App extends Component {
    constructor(props) {
        super(props);
        let currents = window.location.pathname;
        console.log(currents);
        this.state = {index: '1',sendCount:0,account:null};

        this.sendSwordParent = this.sendSwordParent.bind(this);
        this.getSwordCount = this.getSwordCount.bind(this);




        //class
        if (currents == '/app/GameIntroduced') {
            this.state.index='2';
        }
        if (currents == '/app/Company') {
            this.state.index='3'
        }
        if (currents == '/app/ContactUs') {
            this.state.index='4'
        }
        if(currents=='/app/Recruit') {
            this.state.index='5'
        }
        if(currents=='/app/ParentsCare'){
            this.state.index='6'
        }
        if(currents=='/app/Login'){
            this.state.index='7'
        }
        if(currents=='/app/Register'){
            this.state.index='8'
        }
        if(currents=='/app/System'){
            this.state.index='9'
        }
        if(currents=='/app/Protocol'){
            this.state.index='10'
        }
        if(currents=='/app/Scheme'){
            this.state.index='11'
        }
        if(currents=='/app/Dispute'){
            this.state.index='12'
        }
    }


    //
    //propsstatepropsstate


    componentWillUpdate(e){
        console.log(sessionStorage.getItem("account"));
        //this.state.account=sessionStorage.getItem("account");
        if(e.location.pathname=='/app/Login'){
            this.state.index='7';
        }
        if(e.location.pathname=='/app/Register'){
            this.state.index='8';
        }
    }

    sendSwordParent () {
        // 
            this.refs.getSwordButton.sendSword();
        //console.log(' this.refs.getSwordButton', this.refs.getSwordButton);
    }

    getSwordCount (refs) {
        //  this.refs.getSwordButton    this.refs.getSwordButton state.count 
        console.log(this.refs.getSwordButton);
        this.setState({sendCount: this.refs.getSwordButton.state.count + 1}, () => {
                        console.log(this.refs.getSwordButton.state.count);
            this.refs.getSwordButton.setState({
                count: this.refs.getSwordButton.state.count + 1
            });
        });
    }


    //class
    //
    indexClick=()=>{
        this.setState({index:'1'})
    };

    //
    GameIntroducedClick=()=>{
        this.setState({index:'2'})
    };

    //
    CompanyClick=()=>{
        this.setState({index:'3'})
    };

    //
    ContactUsClick=()=>{
        this.setState({index:'4'})
    };

    //
    RecruitClick=()=>{
        this.setState({index:'5'})
    };

    //
    ParentsCareClick=()=>{
        this.setState({index:'6'})
    };

    //
    LoginClick=()=>{
        this.setState({index:'7'})
    };

    //
    RegisterClick=()=>{
        this.setState({index:'8'})
    };

    //
    SystemClick=()=>{
        this.setState({index:'9'})
    };

    //
    ProtocolClick=()=>{
        this.setState({index:'10'})
    };

    //
    SchemeClick=()=>{
        this.setState({index:'11'})
    };

    //
    DisputeClick=()=>{
        this.setState({index:'12'})
    };


    //
    quit=(e)=>{
        sessionStorage.removeItem('account');
        var nowAddress=this.props.history.location.pathname;
        this.props.history.push(nowAddress);
        //window.location.href=window.location.href;
    };


  render() {
      let match = this.props.match;
      let value=sessionStorage.getItem("account");
      return (
      <div className="App">
          <div className="tpri">
              <div className="logo1">
                  <Link to={`${match.url}`} className="yuxun_logo" onClick={this.indexClick}>
                      <img src={jxtlogo} alt="" width="110" height="70" />
                  </Link>
                  <div className="box">

                      {/*,404*/}
                      {match.path!='/app' ?
                          <ul className="nav clearfix">
                              <li><Link className={this.state.index=='1' ? 'active':''} to={`${match.url}app`} onClick={this.indexClick}> </Link></li>
                              <li><Link className={this.state.index=='2' ? 'active':''} to={`${match.url}app/GameIntroduced`} onClick={this.GameIntroducedClick}></Link></li>
                              <li><Link className={this.state.index=='3' ? 'active':''} to={`${match.url}app/Company`} onClick={this.CompanyClick}></Link></li>
                              <li><Link className={this.state.index=='4' ? 'active':''} to={`${match.url}app/ContactUs`} onClick={this.ContactUsClick}></Link></li>
                              <li><Link className={this.state.index=='5' ? 'active':''} to={`${match.url}app/Recruit`} onClick={this.RecruitClick}></Link></li>
                              <li><Link className={this.state.index=='6' ? 'active':''} to={`${match.url}app/ParentsCare`} onClick={this.ParentsCareClick}></Link></li>
                              {value!=null ?
                                  <li id="last">
                                      {'hello,'+value} <span className="lastSpan" onClick={this.quit}></span>
                                  </li>
                                  :
                                  <li id="last">
                                      <Link className={this.state.index=='7' ? 'active':''} to={`${match.url}app/Login`} onClick={this.LoginClick}></Link>
                                      <span className="navSpan">|</span>
                                      <Link className={this.state.index=='8' ? 'active':''} to={`${match.url}app/Register`} onClick={this.RegisterClick}></Link></li>
                              }
                          </ul>
                            :
                          <ul className="nav clearfix">
                              <li><Link className={this.state.index=='1' ? 'active':''} to={`${match.url}`} onClick={this.indexClick}> </Link></li>
                              <li><Link className={this.state.index=='2' ? 'active':''} to={`${match.url}/GameIntroduced`} onClick={this.GameIntroducedClick}></Link></li>
                              <li><Link className={this.state.index=='3' ? 'active':''} to={`${match.url}/Company`} onClick={this.CompanyClick}></Link></li>
                              <li><Link className={this.state.index=='4' ? 'active':''} to={`${match.url}/ContactUs`} onClick={this.ContactUsClick}></Link></li>
                              <li><Link className={this.state.index=='5' ? 'active':''} to={`${match.url}/Recruit`} onClick={this.RecruitClick}></Link></li>
                              <li><Link className={this.state.index=='6' ? 'active':''} to={`${match.url}/ParentsCare`} onClick={this.ParentsCareClick}></Link></li>
                              {value!=null ?
                                  <li id="last">
                                      {'hello,'+value} <span className="lastSpan" onClick={this.quit}></span>
                                  </li>
                                  :
                                  <li id="last">
                                      <Link className={this.state.index=='7' ? 'active':''} to={`${match.url}/Login`} onClick={this.LoginClick}></Link>
                                      <span className="navSpan">|</span>
                                      <Link className={this.state.index=='8' ? 'active':''} to={`${match.url}/Register`} onClick={this.RegisterClick}></Link></li>
                              }
                          </ul>
                      }

                  </div>
              </div>
          </div>
          <div className="react-main">
              {/*<Test ref="getSwordButton" getSwordCount2={this.getSwordCount} buttonName="" />
              <button onClick={this.sendSwordParent}></button>
              <p style={{fontSize: '20px'}}>
                  {this.state.sendCount}
              </p>*/}

              {/*Switch.
                 Redirect,404
*/}
              <Switch>
              <Route path={`${match.url}`} exact component={Index} />
              <Route path={`${match.url}/GameIntroduced`} component={GameIntroduced} />
              <Route path={`${match.url}/Company`} component={Company} />
              <Route path={`${match.url}/ContactUs`} component={ContactUs} />
              <Route path={`${match.url}/Recruit`} component={Recruit} />
              <Route path={`${match.url}/ParentsCare`} component={ParentsCare} />
              <Route path={`${match.url}/Login`} component={Login} />
              <Route path={`${match.url}/Register`} component={Register} />
              <Route path={`${match.url}/System`} component={System} />
              <Route path={`${match.url}/Protocol`} component={Protocol} />
              <Route path={`${match.url}/Scheme`} component={Scheme} />
              <Route path={`${match.url}/Dispute`} component={Dispute} />
              <Redirect to='/404' />
              </Switch>
          </div>
          <div className="footer-wrap">
             <div className="footer-content">
                  <div className="nav">
                      {match.path!='/app' ?
                          <p>
                              <Link to={`${match.url}app/Company`} onClick={this.CompanyClick}></Link>|
                              <Link to={`${match.url}app/ContactUs`} onClick={this.ContactUsClick}></Link>|
                              <Link to={`${match.url}app/Recruit`} onClick={this.RecruitClick}></Link>|
                              <Link to={`${match.url}app/System`} onClick={this.SystemClick}></Link>|
                              <Link to={`${match.url}app/GameIntroduced`} onClick={this.GameIntroducedClick}></Link>|
                              <Link to={`${match.url}app/Protocol`} onClick={this.ProtocolClick}></Link>|
                              <Link to={`${match.url}app/Scheme`} onClick={this.SchemeClick}></Link>|
                              <Link to={`${match.url}app/Dispute`} onClick={this.DisputeClick}></Link>|
                              <Link to={`${match.url}app/ParentsCare`} onClick={this.ParentsCareClick}></Link>
                          </p>
                            :
                          <p>
                              <Link to={`${match.url}/Company`} onClick={this.CompanyClick}></Link>|
                              <Link to={`${match.url}/ContactUs`} onClick={this.ContactUsClick}></Link>|
                              <Link to={`${match.url}/Recruit`} onClick={this.RecruitClick}></Link>|
                              <Link to={`${match.url}/System`} onClick={this.SystemClick}></Link>|
                              <Link to={`${match.url}/GameIntroduced`} onClick={this.GameIntroducedClick}></Link>|
                              <Link to={`${match.url}/Protocol`} onClick={this.ProtocolClick}></Link>|
                              <Link to={`${match.url}/Scheme`} onClick={this.SchemeClick}></Link>|
                              <Link to={`${match.url}/Dispute`} onClick={this.DisputeClick}></Link>|
                              <Link to={`${match.url}/ParentsCare`} onClick={this.ParentsCareClick}></Link>
                          </p>
                      }
                  </div>
                  <div className="footer-bottom">
                      <img src={jxtlogo} alt="" />
                          <div className="footer-bottom-text">
                              <p>  ICP17012908-1 Copyright  2007-2018</p>
                              <p></p>
                          </div>
                  </div>
              </div>
          </div>

      </div>
    );
  }
}

export default App;



